<template>
  <div>
    <b-card
      ref="cardAction"
      title="All Companies"
    >
      <b-row>
        <b-col cols="12">
          <b-table
            id="my-table"
            :per-page="perPage"
            :current-page="currentPage"
            head-variant
            hover
            responsive
            :items="getCategories"
            :fields="fields"
            bordered
          >
            <template #head()="data">
              <span
                class="text-primary"
                style="font-size:18px"
              >{{ data.label }}</span>
            </template>

            <template #table-busy>
              <div class="text-center text-dark my-2">
                <b-spinner class="align-middle" />
                <strong> Loading... </strong>
              </div>
            </template>

            <template #cell(id)="row">
              {{ row.item.id }}
            </template>

            <template #cell(image)="row">
              <b-avatar
                rounded
                variant="transparent"
                size="4rem"
                class="mr-1"
                :src="row.item.image"
              />
            </template>

            <template #cell(name)="row">
              {{ row.item.name }}
            </template>

            <template #cell(type)="row">
              {{ row.item.type === 1 ? 'جوال': 'لوحة تحكم' }}
            </template>

            <template #cell(status)="row">
              {{ status(row.item.status) }}
            </template>

            <template #cell(actions)="row">
              <b-button
                class="mr-1 mb-1"
                size="sm"
                variant="primary"
                @click="edit(row.item)"
              > Edit </b-button>
              <b-button
                class="mb-1"
                size="sm"
                variant="danger"
                @click="confirmDelete(row.item.id)"
              > Delete </b-button>
            </template>
          </b-table>
        </b-col>
      </b-row>
      <div class="mt-3">
        <b-pagination
          v-model="currentPage"
          :total-rows="rows"
          align="left"
          :per-page="perPage"
          aria-controls="my-table"
        />
      </div>
    </b-card>
    <edit-category :obj="Obj" />
  </div>
</template>

<script>
import EditCategory from '@/views/admin/companies/EditCompany.vue'
import {
  BRow, BCol, BTable, BButton, BPagination, BCard, BAvatar,
} from 'bootstrap-vue'
import { faEye, faTrash, faPlus } from '@fortawesome/free-solid-svg-icons'
import { library } from '@fortawesome/fontawesome-svg-core'

library.add(faEye, faTrash, faPlus)

export default {
  name: 'AllCategory',
  components: {
    BCard,
    BRow,
    BCol,
    BTable,
    BButton,
    BAvatar,
    BPagination,
    EditCategory,
  },
  data() {
    return {
      Obj: {},
      perPage: 10,
      currentPage: 1,
      add: true,
      busy: true,
      file: null,
      fields: [
        { key: 'id', label: 'id' },
        { key: 'name', label: 'name' },
        { key: 'email', label: 'email' },
        { key: 'url', label: 'url' },
        { key: 'actions', label: 'actions' },
      ],
    }
  },
  computed: {
    rows() {
      return this.getCategories.length
    },
    getCategories() {
      return this.$store.state.company.companies
    },
  },
  created() {
    this.$store.dispatch('company/fetch').then(() => {
    })
  },
  methods: {
    edit(data) {
      this.Obj = data
      // console.log(this.Obj)
      this.$bvModal.show('EditCat')
    },
    status(value) {
      const all = [
        {
          id: '1',
          name: 'اعمال حرة',
        },
        {
          id: '2',
          name: 'شركة',
        },
        {
          id: '3',
          name: 'الكل',
        },
      ]
      const item = all.find(x => x.id === value)
      return item ? item.name : ''
    },
    confirmDelete(id) {
      this.$swal({
        title: 'Delete Company',
        text: 'Are you Delete Company ?',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Delete !',
        cancelButtonText: 'Cancel',
        customClass: {
          confirmButton: 'btn btn-primary',
          cancelButton: 'btn btn-outline-danger ml-1',
        },
        buttonsStyling: false,
      }).then(result => {
        if (result.value) {
          this.$store.dispatch('company/delete', id).then(() => {
            this.$swal({
              icon: 'success',
              title: 'Delete Company',
              text: 'The company has been added successfully',
              customClass: {
                confirmButton: 'btn btn-success',
              },
            })
          })
        }
      })
    },
  },
}
</script>

<style scoped></style>
