<template>
  <b-modal
    id="EditCat"
    cancel-variant="outline-secondary"
    ok-title="Update"
    cancel-title="Cancel"
    centered
    size="sm"
    title="Edit Company"
    @ok="update"
  >
    <validation-observer ref="simpleRules">
      <b-form>
        <b-row>
          <!-- name -->
          <b-col md="12">
            <b-form-group
              label="Name"
              label-for="name"
            >
              <validation-provider
                #default="{ errors }"
                name="name"
                rules="required"
              >
                <b-form-input
                  id="name"
                  v-model="dataName"
                  :state="errors.length > 0 ? false : null"
                  placeholder="Name"
                />
                <small class="text-danger">{{ errors[0] }}</small>
              </validation-provider>
            </b-form-group>
          </b-col>
          <!-- email -->
          <b-col md="12">
            <b-form-group
              label="Email"
              label-for="email"
            >
              <validation-provider
                #default="{ errors }"
                name="email"
                rules="required"
              >
                <b-form-input
                  id="email"
                  v-model="dataEmail"
                  :state="errors.length > 0 ? false : null"
                  placeholder="Email"
                />
                <small class="text-danger">{{ errors[0] }}</small>
              </validation-provider>
            </b-form-group>
          </b-col>
          <!-- URL -->
          <b-col md="12">
            <b-form-group
              label="URL"
              label-for="url"
            >
              <validation-provider
                #default="{ errors }"
                name="url"
                rules="required"
              >
                <b-form-input
                  id="url"
                  v-model="dataUrl"
                  :state="errors.length > 0 ? false : null"
                  placeholder="URL"
                />
                <small class="text-danger">{{ errors[0] }}</small>
              </validation-provider>
            </b-form-group>
          </b-col>

          <b-col md="12">
            <b-form-group
              label="Logo"
              label-for="Logo"
            >
              <validation-provider
                #default="{ errors }"
                name="Logo"
                rules="required"
              >
                <b-form-file
                  id="logo"
                  v-model="dataLogo"
                  :state="errors.length > 0 ? false : null"
                  placeholder="Logo"
                  @change="fileCategory"
                />
                <small class="text-danger">{{ errors[0] }}</small>
              </validation-provider>
            </b-form-group>
          </b-col>
          <!--          {{ dataImage }}-->
          <!--          <img-->
          <!--            :src="dataImage"-->
          <!--            alt="logo for company"-->
          <!--          >-->
        </b-row>
      </b-form>
    </validation-observer>
  </b-modal>
</template>

<script>
import { ValidationProvider, ValidationObserver, localize } from 'vee-validate'
import { required } from '@/@core/utils/validations/validations'
import {
  BFormInput, BFormGroup, BForm, BRow, BCol, BFormFile, BModal,
} from 'bootstrap-vue'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

const dictionary = {
  ar: {
    names: {
      name: 'اسم التصنيف',
      type: 'نوع التصنيف',
      image: 'صورة التصنيف',
    },
  },
}
localize('ar', dictionary.ar)
export default {
  name: 'EditCategory',
  components: {
    ValidationProvider,
    ValidationObserver,
    BRow,
    BCol,
    // BFormSelect,
    BFormFile,
    BForm,
    BFormGroup,
    BFormInput,
    BModal,
    // eslint-disable-next-line vue/no-unused-components
    ToastificationContent,
  },
  props: {
    obj: {
      type: Object,
      default: () => {
      },
    },
  },
  data() {
    return {
      dataImage: '',
      dataId: '',
      dataName: '',
      dataEmail: '',
      dataUrl: '',
      dataLogo: [],
      dataStatus: '',
      category_file: null,
      required,
    }
  },
  watch: {
    obj() {
      const {
        id, name, email, url, image,
      } = JSON.parse(JSON.stringify(this.obj))
      this.dataId = id
      this.dataName = name
      this.dataEmail = email
      this.dataUrl = url
      this.dataImage = image
    },
  },
  methods: {
    update() {
      const Obj = new FormData()
      Obj.append('id', this.dataId)
      Obj.append('company_id', this.dataId)
      Obj.append('name', this.dataName)
      Obj.append('email', this.dataEmail)
      Obj.append('url', this.dataUrl)
      // Obj.append('logo', this.dataImage)
      if (typeof this.dataImage === 'object') {
        Obj.append('logo', this.dataImage)
      }

      this.$store.dispatch('company/update', Obj).then(() => {
        this.$toast({
          component: ToastificationContent,
          props: {
            title: 'The company has been updated successfully',
            icon: 'CheckCircleIcon',
            variant: 'success',
          },
        })
      })
    },
    fileCategory(event) {
      // eslint-disable-next-line prefer-destructuring
      this.dataImage = event.target.files[0]
    },
  },
}
</script>

<style scoped>

